.page-main__title {
  margin: 0;
  padding-top: 15px;
  padding-bottom: 28px;
  padding-left: 20px;
  padding-right: 20px;

  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 60px;
  font-weight: 400;
  color: #111111;

  &--program {
    padding-bottom: 25px;
  }
}


@media (min-width: $tablet-width) {
  .page-main__title {
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 53px;

    font-size: 60px;

    &--program {
      padding-bottom: 55px;
    }
  }

  .page-main__item {
    &--first {
      margin-top: 55px;
    }

    &--second {
      margin-bottom: 47px;
    }

    &--profile {
      margin-bottom: 72px;
    }
  }
}

@media (min-width: $desktop-width) {
  .page-main__item--first {
    margin: 0 auto;
    margin-bottom: 80px;
  }

  .page-main__item--second {
    margin: 0 auto;
    margin-bottom: 74px;
  }

  .page-main__item--profile {
    margin-bottom: 50px;
    margin-left: auto;
    margin-right: auto;
  }

  .page-main__title {
    width: 1300px;
    margin: 0 auto;
    padding-left: 40px;
    padding-right: 40px;
    padding-top: 72px;
    padding-bottom: 45px;

    &--program {
      padding-bottom: 55px;
    }
  }
}
